//button
.btn-triangle {
    position: relative;
}

.btn-triangle ::after {
    position: absolute;
    content: " ";
    right: 0;
    bottom: 0;
    border-left: 16px solid transparent;
    border-bottom: 16px solid #E6553B;
}

//Q:a btn-triangle????

.btn-triangle ::after  :hover {
      border-bottom: 16px black solid; //hover?
}

//btn-size
.btn-size {
    width: 132px;
    height: 35px;
}


//button
.btn-white {
    background-color: white;
    border: 1px solid #000;
}

.btn-white:hover {
    background-color: $primary;
    color: white;
    border: 1px solid #000;
}